<template>
    <div>
    <div  class="select">
        
        <el-select v-model="table_type" filterable placeholder="" @change="$forceUpdate()">
        <el-option
            v-for="item in table_type_list"
            :key="item.value"
            :label="item.label"
            :value="item.value"
        ></el-option>
    </el-select>
    PRB(0-99)
    <el-select v-model="prb" filterable placeholder="" @change="$forceUpdate()">
        <el-option
            v-for="item in prb_list"
            :key="item.value"
            :label="item.label"
            :value="item.value"
        ></el-option>
    </el-select>
    
    <el-select v-model="granularity" filterable placeholder="" @change="$forceUpdate()">
        <el-option
            v-for="item in granularity_list"
            :key="item.value"
            :label="item.label"
            :value="item.value"
        ></el-option>
    </el-select>
</div>
    <div class="left-bar">
        <div id="echart" style="width: 100%;height: 100%;"></div>
    </div>
    <div class="right-bar">
    <div class="properities">
        
        <div>
            <span></span>
            <br/>
            <el-date-picker v-model="date1" type="datetime" value-format="yyyy-MM-dd HH" class="datepicker-content" :picker-options="{minuteStep:60}" popper-class="datepickerPopperClass" placeholder="" @change="changeDate(date1)" :clearable="false" ></el-date-picker>
            <br/>~
            <br/>
            <el-date-picker v-model="date2" type="datetime" value-format="yyyy-MM-dd HH" class="datepicker-content" :picker-options="{minuteStep:60}" popper-class="datepickerPopperClass" placeholder="" @change="changeDate(date2)" :clearable="false"></el-date-picker>
            <br/>
            <el-button type="primary" @click="search"></el-button>
        </div>
        </div>
    </div>
    </div>
</template>

<script>
    import axios from 'axios'
import { getToken } from '@/utils/auth'
import * as echarts from 'echarts';
import "./datapicker.css"
const properities_list= [
                {key:"RCCConnSUCC",label:"RRC"},
                {key:"RCCConnATT",label:"RRC"},
                {key:"RCCConnRATE",label:"RRCqf (%)"}
            ];
export default {
    data() {
        return {
            table_type_list: [],
            table_type: '',
            properities_list: properities_list,
            date1: '',
            date2: '',
            echart:"",
            echartdata:[],
            prb_list:[],
            prb:0,
            granularity_list:[
                {value:"perQuarter",label:"15"},
                {value:"perHour",label:""},
                
            ],
            granularity:"perQuarter"
        }
    }, 
    mounted() {
        this.getTableTypeList()
        this.date1="2020-07-17 00"
        this.date2="2020-07-19 00"
        this.prb_list=[]
        for(let i=0;i<100;i++){
            this.prb_list.push({
                value:i,
                label:i
            })
        }
    },
    methods: {
        getTableTypeList() {
            let that = this
            axios({
                url: '/api/query/PRB_ENodeB_name',
                method: 'get',
                headers: {
                    'Authorization': "Bearer "+getToken()
                }
            }).then(res => {
                for (let i = 0; i < res.data.list.length; i++) {
                    that.table_type_list.push({
                        value: res.data.list[i],
                        label: res.data.list[i]
                    })
                }
                that.$forceUpdate()
                console.log(that.table_type_list)
            })
        },
        changeDate(val){
            console.log(val)
        },
        draw() {
            let that = this
            if(this.echart!="")
                this.echart.dispose()
            this.echart = echarts.init(document.getElementById('echart'));
            let dataseries = []
            let xdata = []
            let data=[]
            for(let i=0;i<this.echartdata.count;i++){
                
                xdata.push(this.echartdata.list[i].Time)
                data.push(this.echartdata.list[i].data)
            }
            dataseries = [{
                name: 'PRB ()',
                type: 'line',
                data: data,
                itemStyle : { normal: {label : {show: true}}}
            }]
            this.echart.setOption( 
                {
                    xAxis: {
                        type: 'category',
                        data: xdata
                    },
                    yAxis: {                    
                        type: 'value',
                        scale: true,
                    },
                    series:dataseries,
                    legend: {
                        data: ['PRB ()']
                    },
                    toolbox:{
                        show:true,
                        feature:{
                            saveAsImage:{
                                show:true
                            }
                        }
                    }
                }
            )
            this.echart.resize({
                width: 'auto',
                height: '500%'
            })

        },
        search() {
            let that = this
            axios({
                url: '/api/query/PRB',
                method: 'get',
                headers: {
                    'Authorization': "Bearer "+getToken()
                },
                params:{
                    ENODEB_NAME:that.table_type,
                    Mode:that.granularity,
                    PRB:that.prb,
                    StartTime:that.date1+":00:00",
                    EndTime:that.date2+":00:00"
                }

            }).then(res => {
                console.log(res.data)
                that.echartdata=res.data
                that.draw()
                //that.echartdata=res.data
                //that.draw()
            })
            .catch(err=>{
                console.log(err)
            })
        }
    }
}

</script>

<style scoped>
    .select{
        width: 100%;
    }
    .left-bar{
        width: 80%;
        float: left;
        background-color: #f5f5f5;
    }
    .right-bar{
        width: 20%;
        float: right;
        background-color: #f5f5f5;
        align-content: center;
        align-items: center;
        background: #FFF;
        
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .properities{
        width: 100%;
        margin: 10px;
    }
    .datepicker-content{
        width: 100%;
    }
</style>



